.div {
  display: flex;
  align-items: center;
  align-items: center;
  justify-content: center;
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.login {
  display: flex;
  text-align: center;
  justify-content: center;
  font-family: 'Manrope', sans-serif;
  margin-right: 20px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.04em;
  padding-top: 9px;
  padding-bottom: 9px;
  color: #fef9f9;
  text-decoration: none;
  background-color: #ffc107;
  border: 2px solid #ffc107;
  border-radius: 40px;
}

.register {
  display: flex;
  text-align: center;
  justify-content: center;
  font-family: 'Manrope', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.04em;
  padding-top: 9px;
  padding-bottom: 9px;
  color: #ffc107;
  text-decoration: none;
  background-color: #fef9f9;
  border: 2px solid #ffc107;
  border-radius: 40px;
}

.icon {
  margin-left: 8px;
}
